import { Helmet } from "react-helmet";
import axios from "axios";
import FsLightbox from "fslightbox-react";
import React, { useEffect, useState } from "react";
import * as Icon from "react-feather";
import ProgressiveImage from "react-progressive-image";
import Slider from "react-slick";
import Layout from "../components/Layout";
import Sectiontitle from "../components/Sectiontitle";
import Service from "../components/Service";
import Testimonial from "../components/Testimonial";

function About() {
  const [toggler, setToggler] = useState(false);
  const [information, setInformation] = useState("");
  const [services, setServices] = useState([]);
  const [reviews, setReviews] = useState([]);

  const sliderSettings = {
    dots: false,
    infinite: true,
    arrows: false,
    speed: 500,
    slidesToShow: 2,
    slidesToScroll: 2,
    autoplay: true,
    autoplaySpeed: 6000,
    pauseOnHover: true,
    adaptiveHeight: true,
    responsive: [
      {
        breakpoint: 768,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
        },
      },
    ],
  };

  const handleToggler = (event) => {
    setToggler({
      toggler: !toggler,
    });
  };

  useEffect(() => {
    axios.get("/api/information").then((response) => {
      setInformation(response.data);
    });
    axios.get("/api/services").then((response) => {
      setServices(response.data);
    });
    axios.get("/api/reviews").then((response) => {
      setReviews(response.data);
    });
  }, []);

  return (
    <Layout>
      <Helmet>
        <title>MovrRise</title>
        <meta
          name="description"
          content="MovrRise"
        />
      </Helmet>
      <div className="mi-about-area mi-section mi-padding-top">
        <div className="container">
          <Sectiontitle title="About" />
          <div className="row">
            <div className="col-lg-6">
              <div className="mi-about-image">
                <ProgressiveImage
                  src={information.aboutImage}
                  placeholder="/images/MovrRise.png"
                >
                  {(src) => (
                    <img
                      src={src}
                      alt="aboutimage"
                      onClick={() => handleToggler(!toggler)}
                    />
                  )}
                </ProgressiveImage>
                <span className="mi-about-image-icon">
                  <Icon.ZoomIn />
                </span>
                <FsLightbox
                  toggler={toggler}
                  sources={[information.aboutImageLg]}
                />
              </div>
            </div>
            <div className="col-lg-6">
              <div className="mi-about-content">
                <h3>
                  I am <span className="color-theme">{information.name}</span>
                </h3>
                <p>
                  <span className="color-theme">The Buyback function</span>
                </p>
				<p>
				The MovrRise buyback function is funded by a 6% strategic buyback fee. The tokens collected from the fees are converted into MOVR and securely locked and stored in the MovrRise contract.

				These reserves then become independent of the price of mRISE. The contract is coded so that the MOVR cannot be withdrawn and can only be utilized for the purposes of buying back mRISE coins from the open market and burning them.

				When the buyback function is turned on, the contract automatically buys back and burns tokens after every sell transaction.
				
				 Once the mRISE tokens are bought back, the new MOVR amount is added to the liquidity pool and the mRISE tokens bought are immediately burned.

				This creates a true burn and guarantees the price per token will increase every time the buyback is activated.

				The buybacks are deployed strategically at specific moments to allow MovrRise to maintain a stable price floor during downward market trends, chart manipulation, and whale dumps, securing the long-term success of the project.
				</p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </Layout>
  );
}

export default About;
